<style lang="scss" module>
  .alert {
    margin: 22px 0 0;
  }
</style>

<template>
  <div class="component-container container-fluid">
    <div class="panel panel-default">
      <!-- title -->
      <div class="panel-heading"> - </div>

      <!-- Loading -->
      <div v-if="load.status === 0" class="panel-body text-center">
        <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
        ...
      </div>

      <!-- Body -->
      <div v-else-if="load.status === 1" class="panel-body form-horizontal">
        <blockquote>
          <p></p>
          <footer>CNY 200%  1CNY  2 </footer>
        </blockquote>

        <!--  -->
        <div class="form-group">
          <label for="wallet-ratio" class="col-sm-2 control-label"></label>
          <div class="col-sm-4">
            <div class="input-group">
              <input type="number" name="ratio" class="form-control" id="wallet-ratio" placeholder="" aria-describedby="wallet-ratio-help" min="1" max="1000" step="1" v-model="ratio">
              <span class="input-group-addon" id="basic-addon2">%</span>
            </div>
          </div>
          <div class="col-sm-6">
            <span id="wallet-ratio-help" class="help-block"> 1 - 1000 </span>
          </div>
        </div>

        <!--  -->
        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-4">
            <button v-if="update === true" class="btn btn-primary" type="submit" disabled="disabled">
              <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
              ...
            </button>
            <button v-else type="button" class="btn btn-primary" @click.stop.prevent="updateRatio"></button>
          </div>
        </div>

        <!--  -->
        <div v-show="alert.open" :class="['alert', `alert-${alert.type}`, $style.alert]" role="alert">
          {{ alert.message }}
        </div>

      </div>

      <!-- Loading Error -->
      <div v-else class="panel-body">
        <div class="alert alert-danger" role="alert">{{ load.message }}</div>
        <button type="button" class="btn btn-primary" @click="requestRatio"></button>
      </div>

    </div>
  </div>
</template>

<script>
import request, { createRequestURI } from '../../util/request';
export default {
  data: () => ({
    ratio: 100,
    load: {
      status: 0,
      message: null,
    },
    update: false,
    alert: {
      open: false,
      interval: null,
      type: 'info',
      message: null,
    },
  }),
  methods: {
    /**
     * .
     *
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    requestRatio() {
      this.load.status = 0;
      request.get(
        createRequestURI('wallet/ratio'),
        { validateStatus: status => status === 200 }
      ).then(({ data: { ratio } }) => {
        this.load.status = 1;
        this.ratio = ratio;
      }).catch(({ response: { data: { message: [ message ] = [] } = {} } = {} }) => {
        this.load = {
          status: 2,
          message,
        };
      });
    },

    /**
     * .
     *
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    updateRatio() {
      const ratio = this.ratio;
      this.update = true;
      request.patch(
        createRequestURI('wallet/ratio'),
        { ratio },
        { validateStatus: status => status === 201 }
      ).then(() => {
        this.update = false;
        this.sendAlert('success', '');
      }).catch(({ response: { data: { message: [ message ] = [] } = {} } = {} }) => {
        this.update = false;
        this.sendAlert('danger', message);
      });
    },
    sendAlert(type, message) {
      window.clearInterval(this.alert.interval);
      this.alert = {
        ...this.alert,
        type,
        message,
        open: true,
        interval: window.setInterval(() => {
          window.clearInterval(this.alert.interval);
          this.alert.open = false;
        }, 2000)
      };
    },
  },
  /**
   * .
   *
   * @return {void}
   * @author Seven Du <shiweidu@outlook.com>
   */
  created() {
    this.requestRatio();
  }
};
</script>



