<style lang="css" module>
.container {
  padding-top: 15px;
}
.loadding {
  text-align: center;
  padding-top: 100px;
  font-size: 42px;
}
.loaddingIcon {
  animation-name: "TurnAround";
  animation-duration: 1.4s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.breadcrumbNotActvie {
  color: #3097D1;
  cursor: pointer;
}
.areaTab{
  margin-bottom: 10px;
}
</style>

<template>

  <div class="container-fluid" :class="$style.container">
  <ul class="nav nav-tabs" :class="$style.areaTab">
    <router-link to="/setting/area" tag="li" active-class="active" exact>
      <a href="#"></a>
    </router-link> 
    <router-link to="/setting/hots" tag="li" active-class="active">
      <a href="#"></a>
    </router-link> 
  </ul>

    <!--  -->
    <div v-show="loadding" :class="$style.loadding">
      <span class="glyphicon glyphicon-refresh" :class="$style.loaddingIcon"></span>
    </div>    
    <!--  -->
    <div v-show="!loadding" class="">

      <!--  -->
      <ol v-if="tree" class="breadcrumb">
        <li :class="$style.breadcrumbNotActvie" @click.prevent="selectCurrent(0)"></li>
        <li
          v-for="area in tree"
          :class="area.id === current ? 'active' : $style.breadcrumbNotActvie"
          @click.prevent="selectCurrent(area.id)"
        >
          {{ area.name }}
        </li>
      </ol>

      <!--  -->
      <div v-show="!current" class="alert alert-success" role="alert">
        <p>1. </p>
        <p>2. <strong></strong><strong>3</strong>,appUI</p>
      </div>

      <!--  -->
      <table class="table table-striped">
        <thead>
          <tr>
            <th></th>
            <th>()</th>
            <th></th>
          </tr>
        </thead>
        <tbody>
            <tr v-for="area in list">
              <td>
                <div class="input-group">
                  <input @change.lazy="patchArea(area.id, 'name', $event.target.value)" type="text" class="form-control" placeholder="" :value="area.name">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <input @change.lazy="patchArea(area.id, 'extends', $event.target.value)" type="text" class="form-control" placeholder="" :value="area.extends">
                </div>
              </td>
              <td>
                <button type="button" class="btn btn-primary btn-sm" @click.prevent="selectCurrent(area.id)"></button>
                <button v-if="deleteIds.hasOwnProperty(area.id)" type="button" class="btn btn-danger btn-sm" disabled="disabled">
                  <span class="glyphicon glyphicon-refresh" :class="$style.loaddingIcon"></span>
                </button>
                <button v-else type="button" class="btn btn-danger btn-sm" @click.prevent="deleteArea(area.id)"></button>
              </td>
            </tr>
            <tr>
              <td>
                <div class="input-group">
                  <input v-model="add.name" type="text" class="form-control" placeholder="">
                </div>
              </td>
              <td>
                <div class="input-group">
                  <input v-model="add.extends" type="text" class="form-control" placeholder="">
                </div>
              </td>
              <td>
                <button v-if="!add.loadding" @click.prevent="addArea" type="button" class="btn btn-primary btn-sm"></button>
                <button v-else class="btn btn-primary btn-sm" disabled="disabled">
                  <span class="glyphicon glyphicon-refresh" :class="$style.loaddingIcon"></span>
                </button>
              </td>
            </tr>
        </tbody>
      </table>

      <div v-show="add.error" class="alert alert-danger alert-dismissible" role="alert">
        <button type="button" class="close" @click.prevent="dismisAddAreaError">
          <span aria-hidden="true">&times;</span>
        </button>
        <strong>Error:</strong>
        <p v-for="error in add.error_message">{{ error }}</p>
      </div>

    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import lodash from 'lodash';
import { SETTINGS_AREA } from '../../store/getter-types';
import { SETTINGS_AREA_CHANGE, SETTINGS_AREA_APPEND, SETTINGS_AREA_DELETE, SETTINGS_AREA_CHANGEITEM } from '../../store/types';
import request, { createRequestURI } from '../../util/request';

const AreaComponent = {
  /**
   * .
   *
   * @return {Object}
   * @author Seven Du <shiweidu@outlook.com>
   * @homepage http://medz.cn
   */
  data: () => ({
    current: 0,
    loadding: true,
    add: {
      name: '',
      extends: '',
      loadding: false,
      error: false,
      error_message: {}
    },
    deleteIds: {}
  }),
  /**
   * .
   *
   * @type {Object}
   */
  computed: {
    /**
     *  maoGetters .
     *
     * @type {Object}
     */
    ...mapGetters({
      areas: SETTINGS_AREA
    }),
    /**
     * .
     *
     * @return {Object}
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    tree () {
      const current = this.current;
      if (current === 0) {
        return false;
      }

      return this.getTrees(current);
    },
    /**
     * 
     *
     * @return {Object} [description]
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    list () {
      const current = this.current;
      let trees = [];
      this.areas.forEach(area => {
        if (area.pid === current) {
          trees.push(area);
        }
      });

      return trees;
    }
  },
  /**
   * , .
   *
   * @type {Object}
   */
  methods: {
    /**
     * .
     *
     * @param {Number} pid
     * @return {Array}
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    getTrees (pid) {
      let trees = [];
      this.areas.forEach(area => {
        if (area.id === pid) {
          trees = [
            ...this.getTrees(area.pid),
            area
          ];
        }
      });

      return trees;
    },
    /**
     * id.
     *
     * @param {Number} id
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    selectCurrent (id) {
      if (this.add.loadding) {
        alert('');
        return;
      }

      this.current = id;
      this.add = {
        name: '',
        extends: '',
        loadding: false
      };
      // 
      document.documentElement.scrollTop = document.body.scrollTop = 0;
    },
    /**
     * .
     *
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    addArea () {
      this.add.loadding = true;
      this.add.error = false;

      const data = {
        name: this.add.name,
        extends: this.add.extends,
        pid: this.current
      };

      this.$store.dispatch(SETTINGS_AREA_APPEND, cb => request.post(
        createRequestURI('site/areas'),
        data,
        { validateStatus: status => status === 201 }
      ).then(({ data }) => {
        this.add = {
          name: '',
          extends: '',
          loadding: false
        };
        cb(data);
      }).catch(({ response: { data = {} } = {} }) => {
        const { error = ['!!!'] } = data;
        this.add.loadding = false;
        this.add.error = true;
        this.add.error_message = error;
      }));
    },
    /**
     * .
     *
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    dismisAddAreaError () {
      this.add.error = false;
    },
    /**
     * .
     *
     * @param {Number} id
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    deleteArea (id) {
      if (window.confirm('?')) {
        this.deleteIds = {
          ...this.deleteIds,
          [id]: id
        };

        const deleteId = (id) => {
          let ids = {};
          for (let _id in this.deleteIds) {
            if (parseInt(_id) !== parseInt(id)) {
              ids = {
                ...ids,
                [_id]: _id
              };
            }
          }
          this.deleteIds = ids;
        };

        this.$store.dispatch(SETTINGS_AREA_DELETE, cb => request.delete(
          createRequestURI(`site/areas/${id}`),
          { validateStatus: status => status === 204 }
        ).then(() => {
          cb(id);
          deleteId(id);
        }).catch(({ response: { data = {} } = {} }) => {
          deleteId(id);
          const { error = '' } = data;
          window.alert(error);
        }));
      }
    },
    /**
     * 
     *
     * @author Seven Du <shiweidu@outlook.com>
     * @homepage http://medz.cn
     */
    patchArea (id, key, value) {
      this.$store.dispatch(SETTINGS_AREA_CHANGEITEM, cb => request.patch(
        createRequestURI(`site/areas/${id}`),
        { key, value },
        { validateStatus: status => status === 201 }
      ).then(() => {
        cb({
          id,
          [key]: value
        });
      }).catch(({ response: { data = {} } = {} }) => {
        const { error = [''] } = data;
        const errorMessage = lodash.values(error).pop();
        window.alert(errorMessage);
      }));
    }
  },
  /**
   * .
   *
   * @author Seven Du <shiweidu@outlook.com>
   * @homepage http://medz.cn
   */
  created () {
    this.$store.dispatch(SETTINGS_AREA_CHANGE, cb => request.get(
      createRequestURI('site/areas'),
      // ,  then.
      { validateStatus: status => status === 200 }
    ).then(({ data = [] }) => {
      cb(data);
      this.loadding = false;
    }).catch(({ response: { data: { message = '' } = {} } = {} }) => {
      this.loadding = false;
      window.alert(message);
    }));
  }
};

export default AreaComponent;
</script>



