<style lang="scss" scoped>
  .list-group-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
</style>

<template>
  <div class="component-container container-fluid">
    <div class="panel panel-default">
      <!-- Title -->
      <div class="panel-heading"></div>
      <ul class="list-group">
        <li v-for="(per, index) in system" :key="index" class="list-group-item">
          <span style="font-size: 16px;">{{ translates[index] }} : </span>
          <span>{{ per }}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import { SETTINGS_SYSTEM_UPDATE } from '../../store/types';
  import request, { createRequestURI } from '../../util/request';

  const System = {
    data: () => ({
      translates: {
        php_version: 'PHP',
        os: '',
        server: '',
        domain_ip: '/IP',
        db: '',
        root: '',
        laravel_version: 'Laravel',
        max_upload_size: '',
        server_date: '',
        local_date: '',
        protocol: '',
        port: '',
        method: '',
        execute_time: '',
        agent: '',
        user_ip: 'IP',
        disk: '' 
      }
    }),
    created () {
      this.getSystemInfo();
    },

    computed: {
      system () {
        return this.$store.state.system;
      }
    },

    methods : {
      getSystemInfo () {
        request.get(createRequestURI('site/systeminfo'), {
          validateStatus: status => status === 200
        }).then(({ data = {} }) => {
          this.$store.commit(SETTINGS_SYSTEM_UPDATE, { ...data });
          this.loadding = false;
        }).catch(({ response: { data: { message = '' } = {} } = {} }) => {
          this.loadding = false;
          this.error = true;
          window.alert(message);
        });
      }
    }
  }

  export default System;
</script>


