<template>
  <div class="component-container container-fluid">
    <div class="panel panel-default">

      <!-- Title -->
      <div class="panel-heading"> -  Ping++</div>

      <!-- Loading -->
      <div v-if="load.status === 0" class="panel-body text-center">
        <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
        ...
      </div>

      <!-- Body -->
      <div v-else-if="load.status === 1" class="panel-body form-horizontal">
        <blockquote>
          <p>ThinkSNS+  <a href="https://www.pingxx.com/" target="block">Ping++</a> </p>
          <footer> RSA  OpenSSL  PHP </footer>
        </blockquote>

        <!-- APP ID -->
        <div class="form-group">
          <label class="col-sm-2 control-label"> ID</label>
          <div class="col-sm-4">
            <input type="text" class="form-control" placeholder=" ID" v-model="appId">
          </div>
          <span class="col-sm-6 help-block">
            ID
          </span>
        </div>

        <!-- Secret Key -->
        <div class="form-group">
          <label class="col-sm-2 control-label">Secret Key</label>
          <div class="col-sm-4">
            <input type="text" class="form-control" placeholder=" Secret Key" v-model="secretKey">
          </div>
          <span class="col-sm-6 help-block">
             Secret Key Test Secret Key Live Secret Key
          </span>
        </div>

        <!-- Ping++ public key -->
        <div class="form-group">
          <label class="col-sm-2 control-label">Ping++ </label>
          <div class="col-sm-4">
            <textarea class="form-control" rows="3" v-model="publicKey"></textarea>
          </div>
          <span class="col-sm-6 help-block">
             Webhooks 
          </span>
        </div>

        <!-- local private key -->
        <div class="form-group">
          <label class="col-sm-2 control-label"></label>
          <div class="col-sm-4">
            <textarea class="form-control" rows="3" v-model="privateKey"></textarea>
          </div>
          <span class="col-sm-6 help-block">
             Ping++ <a href=""></a>  Ping++ 
          </span>
        </div>

        <!--  -->
        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-10">
            <button v-if="updating" type="button" class="btn btn-primary" disabled="disabled">
              <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
            </button>
            <button v-else type="button" class="btn btn-primary" @click="updateConfig"></button>
          </div>
        </div>

        <!--  -->
        <div v-show="alert.status" :class="['alert', `alert-${alert.type}`]" style="margin-top: 16px;" role="alert">
          {{ alert.message }}
        </div>

      </div>

      <!-- Load Error -->
      <div v-else class="panel-body">
        <div class="alert alert-danger" role="alert">{{ load.message }}</div>
        <button type="button" class="btn btn-primary" @click="requestConfig"></button>
      </div>

    </div>
  </div>
</template>
  
<script>
import request, { createRequestURI } from '../../util/request';
export default {
  data: () => ({
    appId: null,
    secretKey: null,
    publicKey: null,
    privateKey: null,
    load: {
      status: 0,
      message: ''
    },
    alert: {
      status: false,
      type: 'info',
      message: '',
      interval: null
    },
    updating: false
  }),
  methods: {
    /**
     * Request Ping++ config.
     *
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    requestConfig() {
      this.load.status = 0;
      request.get(
        createRequestURI('wallet/pingpp'),
        { validateStatus: status => status === 200 }
      ).then(({ data: { app_id: appId, secret_key: secretKey, public_key: publicKey, private_key: privateKey } = {} }) => {
        this.load.status = 1;
        this.appId = appId;
        this.secretKey = secretKey;
        this.publicKey = publicKey;
        this.privateKey = privateKey;
      }).catch(({ response: { data: { message: [ message ] = [] } = {} } = {} } = {}) => {
        this.load = {
          status: 2,
          message
        };
      });
    },

    /**
     * Update Ping++ config.
     *
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    updateConfig() {
      this.updating = true;
      request.patch(
        createRequestURI('wallet/pingpp'),
        { app_id: this.appId, secret_key: this.secretKey, public_key: this.publicKey, private_key: this.privateKey },
        { validateStatus: status => status === 201 }
      ).then(({ data: { message: [ message = '' ] = [] } }) => {
        this.updating = false;
        this.sendAlert('success', message);
      }).catch(({ response: { data: { app_id: appIdMessage = [], secret_key: secretKeyMessage = [], public_key: publicKeyMessage = [], private_key: privateKeyMessage = [], message: anyMessage = [] } = {} } = {} } = {}) => {

        const [ message = '' ] = [ ...appIdMessage, ...secretKeyMessage, ...publicKeyMessage, ...privateKeyMessage, ...anyMessage ];
        this.updating = false;
        this.sendAlert('danger', message);
      });
    },

    /**
     * Send alert message tip.
     *
     * @param {String} type
     * @param {String} message
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    sendAlert(type, message) {
      window.clearInterval(this.alert.interval);
      this.alert = { type, message, status: true, interval: window.setInterval(() => {
        window.clearInterval(this.alert.interval);
        this.alert.status = false;
      }, 1500) };
    }
  },
  /**
   * The page created.
   *
   * @return {void}
   * @author Seven Du <shiweidu@outlook.com>
   */
  created() {
    this.requestConfig();
  }
};
</script>



