<template>
  <div class="component-container container-fluid">
    <div class="panel panel-default">
      <!-- Title -->
      <div class="panel-heading"></div>

      <!-- Search -->
      <div class="panel-body form-horizontal">

        <!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label"></label>
          <div class="col-sm-4">
            <input type="number" class="form-control" v-model="search.user">
          </div>
          <span class="help-block col-sm-6"> ID</span>
        </div>

        <!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label"></label>
          <div class="col-sm-4">
            <input type="text" class="form-control" v-model="search.account">
          </div>
          <span class="col-sm-6 help-block">ID</span>
        </div>

        <!-- Ping++  -->
        <div class="form-group">
          <label class="col-sm-2 control-label">Ping++ </label>
          <div class="col-sm-4">
            <input type="text" class="form-control" v-model="search.chargeId">
          </div>
          <span class="col-sm-6 help-block"> Ping++  ID</span>
        </div>

        <!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label"> ID</label>
          <div class="col-sm-4">
            <input type="text" class="form-control" v-model="search.transactionNo">
          </div>
          <span class="col-sm-6 help-block"> ID</span>
        </div>

        <!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label"></label>
          <div class="col-sm-4">
            <select class="form-control" v-model="search.action">
              <option :value="null"></option>
              <option value="1"></option>
              <option value="0"></option>
            </select>
          </div>
          <span class="col-sm-6 help-block"></span>
        </div>

        <!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label"></label>
          <div class="col-sm-4">
            <select class="form-control" v-model="search.status">
              <option :value="null"></option>
              <option value="0"></option>
              <option value="1"></option>
              <option value="2"></option>
            </select>
          </div>
          <span class="col-sm-6 help-block"></span>
        </div>

        <!--  -->
        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-10">
            <router-link class="btn btn-primary" tag="button" :to="{ path: '/wallet/accounts', query: searchQuery }"></router-link>
          </div>
        </div>

      </div>

      <!-- Loading -->
      <div v-if="load.status === 0" class="panel-body text-center">
        <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
        ...
      </div>

      <!-- List table -->
      <table v-else-if="load.status === 1" class="table table-striped table-hover">
        <thead>
          <tr>
            <th>ID</th>
            <th>ID</th>
            <th></th>
            <th></th>
            <th>Ping++</th>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="charge in charges" :key="charge.id" :class="charge.status === 2 ? 'danger' : charge.status === 1 ? 'success' : ''">
            <td>{{ charge.id }}</td>
            <td>{{ resolveChargeUserDisplay(charge.user) }}</td>
            <td>{{ resolvePayChannel(charge.channel) }}</td>
            <td>{{ charge.account }}</td>
            <td>{{ charge.charge_id }}</td>
            <td>{{ charge.transaction_no }}</td>
            <td>{{ charge.action == 1 ? '+' : '-' }}{{ charge.amount / 100 }}</td>
            <td>
              {{ charge.subject }}<br>
              <small>{{ charge.body }}</small>
            </td>
            
            <!--  -->
            <td v-if="charge.status === 0"></td>
            <td v-else-if="charge.status === 1"></td>
            <td v-else-if="charge.status === 2"></td>
            <td v-else></td>

            <td>{{ charge.created_at }}</td>
          </tr>
        </tbody>
      </table>

      <!-- Load error -->
      <div v-else class="panel-body">
        <div class="alert alert-danger" role="alert">{{ load.message }}</div>
        <button type="button" class="btn btn-primary" @click="requestRefresh"></button>
      </div>

    </div>

    <!--  -->
    <div class="text-center" v-show="pagination.show">
      <ul class="pagination">
        <!--  -->
        <router-link
          v-show="!!pagination.isPrevPage"
          tag="li"
          :to="resolvePaginationRoute(pagination.isPrevPage)"
        >
          <a aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </router-link>

        <!--  -->
        <router-link
          v-for="item in pagination.prevPages"
          tag="li"
          :key="item.page"
          :to="resolvePaginationRoute(item.page)"
        >
          <a>{{ item.name }}</a>
        </router-link>

        <!--  -->
        <router-link class="active" tag="li" :to="resolvePaginationRoute(pagination.current)">
          <a>
            {{ pagination.current }}
          </a>
        </router-link>

        <!--  -->
        <router-link
          v-for="item in pagination.nextPages"
          tag="li"
          :key="item.page"
          :to="resolvePaginationRoute(item.page)"
        >
          <a>{{ item.name }}</a>
        </router-link>

        <!--  -->
        <router-link
          v-show="!!pagination.isNextPage"
          tag="li"
          :to="resolvePaginationRoute(pagination.isNextPage)"
        >
          <a aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </router-link>
        
      </ul>
    </div>

  </div>
</template>

<script>
import lodash from 'lodash';
import request, { createRequestURI } from '../../util/request';

const channelTypes = {
  applepay_upacp: 'Apple Pay',
  alipay: ' APP',
  alipay_pc_direct: '',
  alipay_qr: '',
  alipay_wap: '',
  wx: ' APP',
  wx_wap: ' WAP'
};

export default {
  data: () => ({
    // Search state.
    search: {
      user: null, // 
      account: null, // 
      chargeId: null, // Ping++ ID
      transactionNo: null, // ID
      action: null, // 
      status: null // 
    },

    // Page
    page: {
      current: 1,
      total: 1,
    },

    // 
    charges: [],

    // Load
    load: {
      status: 0,
      message: ''
    },
  }),
  computed: {
    /**
     * Compute search query.
     *
     * @return {Object}
     * @author Seven Du <shiweidu@outlook.com>
     */
    searchQuery() {
      return lodash.reduce(this.search, function (search, value, key) {
        if (!! value) {
          search[key] = value;
        }
        return search;
      }, {});
    },
    /**
     * Pagination.
     *
     * @return {Object}
     * @author Seven Du <shiweidu@outlook.com>
     */
    pagination() {
      // 
      let current = 1;
      current = isNaN(current = parseInt(this.page.current)) ? 1 : current;

      // 
      let last = 1;
      last = isNaN(last = parseInt(this.page.total)) ? 1 : last;

      // 
      const show = last > 1;

      // 
      let minPage = current - 3;
      minPage = minPage <= 1 ? 1 : minPage;

      // 
      let maxPage = current + 3;
      maxPage = maxPage >= last ? last : maxPage;

      // 
      let isPrevPage = false;
      // 
      let prevPages = [];

      // 
      if (current > minPage) {
        // 
        isPrevPage = current - 1; // .

        // 
        if (minPage > 1) {
          prevPages.push({
            name: current < 6 ? 1 : '1...',
            page: 1,
          });
        }

        // 
        for (let i = minPage; i < current; i++) {
          prevPages.push({
            name: i,
            page: i
          });
        }
      }

      // 
      let isNextPage = false;
      // 
      let nextPages = [];

      // 
      if (current < maxPage) {
        // 
        for (let i = current + 1; i <= maxPage; i++) {
          nextPages.push({
            name: i,
            page: i,
          });
        }

        // 
        if (maxPage < last) {
          nextPages.push({
            name: current + 4 === last ? last : '...'+last,
            page: last,
          })
        }

        // 
        isNextPage = current + 1;
      }

      return {
        isPrevPage,
        isNextPage,
        current,
        show,
        prevPages,
        nextPages,
      }
    }
  },
  watch: {
    /**
     * The this.$route watcher.
     *
     * @param {Object} options.query
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    '$route': function ({ query = {} } = {}) {
      this.requestCharge(
        this.resolveQuery(query)
      );
    }, 
  },
  methods: {
    /**
     * Builder page route object.
     *
     * @param {Number} page
     * @return {Object}
     * @author Seven Du <shiweidu@outlook.com>
     */
    resolvePaginationRoute(page) {
      return {
        path: '/wallet/accounts',
        query: { ...this.resolveQuery(), page }
      };
    },

    /**
     * .
     *
     * @param {String} channel
     * @return {String}
     * @author Seven Du <shiweidu@outlook.com>
     */
    resolvePayChannel(channel) {
      const { [channel]: displayChannel = '' } = channelTypes;

      return displayChannel;
    },
    /**
     * .
     *
     * @param {Object} user
     * @return {String|null}
     * @author Seven Du <shiweidu@outlook.com>
     */
    resolveChargeUserDisplay(user = {}) {
      const { id, name } = user;
      if (!! id) {
        return `${name} (${id})`;
      }

      return null;
    },
    /**
     * Request refresh.
     *
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    requestRefresh() {
      this.requestCharge({
        page: this.page.current,
        ...this.resolveQuery(),
      });
    },
    /**
     * Resolve route query.
     *
     * @param {Object} query
     * @return {Object}
     * @author Seven Du <shiweidu@outlook.com>
     */
    resolveQuery(query = this.$route.query) {
      return lodash.reduce(query, function (query, value, key) {
        switch (key) {
          case 'user':
          case 'action':
          case 'status':
            if (!! value) {
              query[key] = isNaN(value = parseInt(value)) ? null : value;
              break;
            }
            query[key] = value;
            break;

          case 'page':
            query[key] = (isNaN(value = parseInt(value)) || value <= 1) ? 1 : value;

          default:
            query[key] = value;
            break;
        }

        return query;
      }, {});
    },

    /**
     * Request the charges.
     *
     * @param {Object} params
     * @return {void}
     * @author Seven Du <shiweidu@outlook.com>
     */
    requestCharge(params = {}) {
      this.load.status = 0;
      request.get(
        createRequestURI('wallet/charges'),
        { params, validateStatus: status => status === 200 }
      ).then(({ data: { total = 1, current = 1, items = [] } }) => {
        this.load.status = 1;
        this.page = { total, current };
        this.charges = items;
      }).catch(({ response: { data: { message: [ anyMessage = '' ] = [] } = {} } = {} } = {}) => {
        this.load = { message: anyMessage, status: 2 };
      });
    }
  },
  /**
   * The component created handle.
   * Init the component.
   *
   * @return {void}
   * @author Seven Du <shiweidu@outlook.com>
   */
  created() {
    const { page = 1, ...query } = this.resolveQuery();
    this.search = {
      ...this.search,
      ...query
    };
    this.page.current = page;

    // Request Charges.
    this.requestCharge({ ...query, page });
  }
};
</script>



