<style lang="css" module>
.container {
  padding-top: 15px;
}
.loadding {
  text-align: center;
  padding-top: 100px;
  font-size: 42px;
}
.loaddingIcon {
  animation-name: "TurnAround";
  animation-duration: 1.4s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
.areaTab{
  margin-bottom: 10px;
}
</style>

<template>

  <div class="container-fluid" :class="$style.container">
  <ul class="nav nav-tabs" :class="$style.areaTab">
    <router-link to="/setting/area" tag="li" active-class="active">
      <a href="#"></a>
    </router-link>
    <router-link to="/setting/hots" tag="li" active-class="active" exact>
      <a href="#"></a>
    </router-link>
  </ul>

    <!--  -->
    <div v-show="loadding" :class="$style.loadding">
      <span class="glyphicon glyphicon-refresh" :class="$style.loaddingIcon"></span>
    </div>
    <!--  -->
    <div v-if="!message" v-show="!loadding">

      <!--  -->
      <div class="alert alert-success" role="alert">
        <p>   </p>
      </div>

      <!--  -->
      <table class="table table-striped">
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
            <tr v-for="item in list">
              <td>
                <div class="input-group">{{item}}</div>
              </td>
              <td>
                <button type="button" class="btn btn-danger btn-sm" @click.prevent="deleteArea(item)"></button>
              </td>
            </tr>
            <tr>
              <td>
                <div class="input-group">
                  <input v-model="add.content" type="text" class="form-control" placeholder="">
                </div>
              </td>
              <td>
                <button v-if="!add.loadding" @click.prevent="doHotsArea" type="button" class="btn btn-primary btn-sm"></button>
                <button v-else class="btn btn-primary btn-sm" disabled="disabled">
                  <span class="glyphicon glyphicon-refresh" :class="$style.loaddingIcon"></span>
                </button>
                <span :class="`text-${add.type}`">{{ add.message }}</span>
              </td>
            </tr>
        </tbody>
      </table>
    </div>
    <!-- Loading Error -->
      <div v-else class="panel-body">
        <div class="alert alert-danger" role="alert">{{ message }}</div>
        <button type="button" class="btn btn-primary" @click.stop.prevent="request"></button>
      </div>
  </div>
</template>

<script>
import request, { createRequestURI } from '../../util/request';

const HotsComponent = {

  data: () => ({
    loadding: false,
    message: '',
    add: {
      content: '',
      message: '',
      update: 0,
      loadding: false,
      type: 'muted'
    },
    list: {}
  }),
  methods: {
    doHotsArea () {
      this.add.loadding = true;
      const data = {
        content: this.add.content,
        update: this.add.update
      };
      request.post(
          createRequestURI('site/areas/hots'),
          data,
          { validateStatus: status => status === 201 }
        ).then(({ data: { message = '', status = '' } }) => {
          const index = this.list.indexOf(this.add.content);
          this.add.loadding = false;
          this.add.type = 'success';
          if (status == 1 && (index < 0)) {
            this.list.push(this.add.content);
            this.add.message = message;
          } else {
            this.add.message = '';
          }
          if (status == 2) {
            if (index > -1) {
              this.list.splice(index, 1);
            }
            this.add.message = '';
          }
        }).catch(({ response: { data: { error: [ error = '' ] = [] } = {} } = {} }) => {
          this.add.loadding = false;
          this.add.type = 'danger';
          this.add.message = error;
      });
    },
    deleteArea (area) {
      if (window.confirm('?')) {
        this.add.content = area;
        this.add.update = 1;
        this.doHotsArea();
      }
    },
    request() {
      this.loadding = true;
      request.get(
        createRequestURI('site/areas/hots'),
        { validateStatus: status => status === 200 }
      ).then(({ data: { data = {} } }) => {
        this.loadding = false;
        this.list = data;
      }).catch(({ response: { data: { message: [ message = '' ] = [] } = {} } = {} }) => {
        this.loadding = false;
        this.message = message;
      });
    }
  },
  created() {
    window.setTimeout(() => this.request(), 500);
  }
};

export default HotsComponent;
</script>



