<template>
  <div class="component-container container-fluid">
    <div class="panel panel-default">
      <!-- Title -->
      <div class="panel-heading"> 
		<router-link to="/setting/sendmail">
        	<button type="button" class="btn btn-primary btn-xs pull-right"></button>
      	</router-link>      
      </div>
            <!-- Loading -->
      <div v-if="loadding.state === 0" class="panel-body text-center">
        <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
        ...
      </div>
      <!-- Body -->
      <div v-else-if="loadding.state === 1" class="panel-body form-horizontal">
        <!-- SMTP -->
        <div class="form-group">
          <label for="host" class="col-sm-2 control-label">SMTP</label>
          <div class="col-sm-4">
            <input type="text" name="host" class="form-control" id="host" placeholder="SMTP" aria-describedby="host-help" v-model="options.host">
          </div>
          <div class="col-sm-6">
            <span id="host-help" class="help-block">SMTP</span>
          </div>
        </div>
        <!-- SMTP -->
        <div class="form-group">
          <label class="col-sm-2 control-label" for="port">SMTP</label>
          <div class="col-sm-4">
            <input type="text" name="port" class="form-control" id="port" placeholder="SMTP" aria-describedby="port-help" v-model="options.port">
          </div>
          <div class="col-sm-6">
            <span class="help-block" id="port-help">SMTP</span>
          </div>
        </div>
        <!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label" for="address"></label>
          <div class="col-sm-4">
            <input type="text" name="address" class="form-control" id="address" placeholder="" aria-describedby="address-help" v-model="options.from.address">
          </div>
          <div class="col-sm-6">
            <span class="help-block" id="address-help"></span>
          </div>
        </div>
		<!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label" for="name"></label>
          <div class="col-sm-4">
            <input type="text" name="name" class="form-control" id="name" placeholder="" aria-describedby="name-help" v-model="options.from.name">
          </div>
          <div class="col-sm-6">
            <span class="help-block" id="name-help"></span>
          </div>
        </div>
		<!--  -->
        <div class="form-group">
          <label class="col-sm-2 control-label" for="encryption"></label>
          <div class="col-sm-4">
          	<input type="radio" name="encryption" value="tls" v-model="options.encryption">TLS  &nbsp;
            <input type="radio" name="encryption" value="ssl" v-model="options.encryption">SSL	&nbsp;
          </div>
          <div class="col-sm-6">
            <span class="help-block" id="encryption-help"></span>
          </div>
        </div> 
		<!-- SMTP -->
        <div class="form-group">
          <label class="col-sm-2 control-label" for="username">SMTP</label>
          <div class="col-sm-4">
            <input type="text" name="username" class="form-control" id="username" placeholder="SMTP" aria-describedby="username-help" v-model="options.username">
          </div>
          <div class="col-sm-6">
            <span class="help-block" id="username-help">SMTP</span>
          </div>
        </div>
		<!-- SMTP -->
        <div class="form-group">
          <label class="col-sm-2 control-label" for="password">SMTP</label>
          <div class="col-sm-4">
            <input type="password" name="password" class="form-control" id="password" placeholder="SMTP" aria-describedby="password-help" v-model="options.password">
          </div>
          <div class="col-sm-6">
            <span class="help-block" id="password-help">SMTP</span>
          </div>
        </div>                     
        <!-- button -->
        <div class="form-group">
          <div class="col-sm-offset-2 col-sm-4">
            <button v-if="submit.state === true" class="btn btn-primary" type="submit" disabled="disabled">
              <span class="glyphicon glyphicon-refresh component-loadding-icon"></span>
              ...
            </button>
            <button v-else type="button" class="btn btn-primary" @click.stop.prevent="submitHandle"></button>
          </div>
          <div class="col-sm-6 help-block">
            <span :class="`text-${submit.type}`">{{ submit.message }}</span>
          </div>
        </div>
      </div>
      <!-- Loading Error -->
      <div v-else class="panel-body">
        <div class="alert alert-danger" role="alert">{{ loadding.message }}</div>
        <button type="button" class="btn btn-primary" @click.stop.prevent="request"></button>
      </div>
    </div>
  </div>
</template>

<script>
import request, { createRequestURI } from '../../util/request';

const MailComponent = {
  data: () => ({
    loadding: {
      state: 0,
      message: '',
    },
    submit: {
      state: false,
      type: 'muted',
      message: '',
    },
    options: {},
  }),
  methods: {
    request() {
      this.loadding.state = 0;
      request.get(
        createRequestURI('site/mail'),
        { validateStatus: status => status === 200 }
      ).then(({ data = {} }) => {
        this.loadding.state = 1;
        this.options = data;
      }).catch(({ response: { data: { message: [ message = '' ] = [] } = {} } = {} }) => {
        this.loadding.state = 2;
        this.loadding.message = message;
      });
    },
    submitHandle() {
      this.submit.state = true;
      request.patch(
        createRequestURI('site/mail'),
        this.options,
        { validateStatus: status => status === 201 }
      ).then(({ data: { message = '' } }) => {
        this.submit.state = false;
        this.submit.type = 'success';
        this.submit.message = message;
      }).catch(({ response: { data: { message: [ message = '' ] = [] } = {} } = {} }) => {
        this.submit.state = false;
        this.submit.type = 'danger';
        this.submit.message = message;
      });
    }
  },
  created() {
    window.setTimeout(() => this.request(), 500);
  }
};

export default MailComponent;
</script>



